$background-color: #f2f2f2;

body{
  background-color: $background-color;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3em;
}

.stage{
  position: relative;
  top: 0px;
  bottom: 20px;
  left: 20px;
  right: 20px;
  width: 800px;
}

.recipe-item, .recipe-item-selected{
  width: 600px;
  margin: 5px;
  padding-left: 5px;
  border-left-width: 5px;
  border-left-style: solid;
}

.recipe-item > div, .recipe-item-selected > div {
  margin-left: 10px;
}

.recipe-item{
  color: #555555;
  border-left-color: #222222;
  background-color: lightgray;
}

.recipe-item-selected{
  border-left-color: green;
  background-color: #c4ffb3;
}

.recipe-item:hover{
  background-color: #f8f8f8;
  border-left-color: lightgreen;
}

.recipe-item-selected:hover{
  background-color: #f8f8f8;
  border-left-color: darkgreen;
}

.recipe-form > input {
  border-color: black;
  border-style: solid;
  border-width: 0px 0px 3px 0px;
  background-color: $background-color;
  padding-left: 5px;
  width: 100%;
}

.recipe-form > input:valid {
  border-color: limegreen;  
}

.recipe-form > input:invalid {
  border-color: red;
  box-shadow: none;
}

.ingredient-button {
  background-color: $background-color;
  border-style: solid;
  border-color: limegreen;
  padding: 8px;
  margin: 0px 16px;
}

.shopping-vendor-title {
  width:600px;
  margin: 15px 5px 5px;
  padding-left: 5px;
  background-color: #dddddd;
}

.shopping-vendor {
  background-color: #bbbbbb;
  width: 100%;
}

.shopping-vendor-toggle{
  min-width: 60px;
  margin: 15px 5px 5px;
  padding-left: 5px;
  background-color: #999999;
  text-align: right;
  cursor: pointer;
}

.shopping-item, .shopping-item-selected{
  width: 600px;
  margin: 5px;
  padding-left: 10px;
  border-left-width: 5px;
  border-left-style: solid;
}

.shopping-item{
  color: white;
  border-left-color: orangered;
  background-color: orange;
}

.shopping-item-selected{
  color: #555555;
  border-left-color: black;
  background-color: lightgrey;
  text-decoration: line-through;
}

.shopping-item:hover{
  color: #555555;
  background-color: #f8f8f8;
  border-left-color: red;
}

.shopping-item-selected:hover{
  background-color: #f8f8f8;
  border-left-color: black;
  text-decoration: line-through;
}

.shopping-item-quantity{
  color: white;
}

.shopping-item-quantity-needed{
  color: gray;
}

.shopping-item-quantity-obtained{
  color: limegreen;
}




